import React, {Component} from "react";
import {connect} from "react-redux";


export default class CustomerVerificationProductLineComponent extends Component{
    constructor(props){
        super(props);
    }

    render() {
        return (
            <div>
                <table role="presentation" class="opl-checkout-header u-small-padding-s">
                    <tbody>
                       <tr>
                           <td colspan="4" class="u-small-no-padding-b">
                                <div>
                                    <b>{this.props.entry.monthlyPrices && this.props.entry.monthlyPrices[0] ? this.props.entry.monthlyPrices[0].gross : ""}</b> <b>{this.props.entry.planName}</b> (Nr telefonu {this.props.entry.msisdn}, {this.props.entry.loyaltyLengthDescription})
                                    {this._renderTerminalInfo()}
                                    {this._renderEuroSetInfo()}
                                </div>
                            </td>
                            {this.props.deposit ?
                            (
                                <td>
                                    <span className="u-right">{this.props.deposit} zł</span>
                                </td>
                            ) : null}
                        </tr>
                    </tbody>
                </table>
                {this.props.deposit ?
                    (
                        <div>
                            <div className="l-col l-col-12 u-padding-all-m u-no-padding-r u-no-padding-l">
                                <div className="o-separator"></div>
                            </div>
                            <div className="l-col l-col-3 u-left u-no-padding-left u-text-left">
                                <span><b>Powód: </b></span>
                            </div>
                            <span>
                                {this._renderReasonSection()}
                            </span>
                        </div>
                    ) : null
                }
                <div className="l-col l-col-12 u-padding-all-m u-no-padding-r u-no-padding-l">
                    <div className="o-separator u-no-padding-all"></div>
                </div>
            </div>
            );
    }

    _renderReasonSection() {
        return (
            <div className="l-col l-col-12 u-padding-all-m u-no-padding-r u-no-padding-l">
                {
                    (this.props.reasons) ?
                    (
                        this.props.reasons.map((reason,index) => {
                            if(this.props.entry.bundleNo === reason.bundleNo) {
                                return (
                                    <div><span>{reason.code}</span></div>
                                )
                            }
                        })
                    )
                    : null
                }
            </div>
        )
    }

    _renderTerminalInfo(){
        return (
            <div>
                {
                    (this.props.entry && this.props.entry.terminals) ?
                    (
                        this.props.entry.terminals
                            .filter(t => !!t)
                            .map((terminal, index) => {
                                return (
                                    <div key={index}><b>{`${terminal.name}${!!terminal.colorDefinition ? ', ' + terminal.colorDefinition.name : ''}`}</b></div>
                                )
                            })
                    )
                    : null
                }
            </div>
        )
    }

    _renderEuroSetInfo(){
            return (
               <div>
                   {
                       (this.props.entry && this.props.entry.euroSets) ?
                       (
                           this.props.entry.euroSets
                               .filter(t => !!t)
                               .map((euroSet, index) => {
                                   return (
                                       <div key={index}><b>{euroSet.name} ({this.euroSetColors(euroSet)})</b></div>
                                   )
                               })
                       )
                       : null
                   }
               </div>
            )
        }

    euroSetColors(euroSet){
        var colors = "";
        var count = 0;
        if(euroSet && euroSet.setElements){
            euroSet.setElements.map((euroSetElement) => {
                count = count + 1;
                colors = colors + euroSetElement.color + (count !== euroSet.setElements.length ? "," : "");
            });
        }

        return colors;
    }
}